<template>
  <div id="app">
    <div class="list-buttons">
      <button :class="getClass('scaleup')" @click="changeSpinner('scaleup')">ScaleUpSpinner </button>
      <button :class="getClass('rotating')" @click="changeSpinner('rotating')">RotationSpinner </button>
      <button :class="getClass('linear')" @click="changeSpinner('linear')">LinearRotationSpinner </button>
      <button :class="getClass('breathing')" @click="changeSpinner('breathing')">BreathingSpinner </button>
      <button :class="getClass('jumping')" @click="changeSpinner('jumping')">JumpingSpinner </button><br>
      <button class="buttons" @click="showCode(type)">Show Code </button>
    </div>
    <div :style="spinnerContainer()" class="spinner-container">
      <ScaleUpSpinner v-if="type=='scaleup'" :src="src"/>
      <RotatingSpinner v-if="type=='rotating'" :src="src"/>
      <LinearRotatingSpinner v-if="type=='linear'" :src="src"/>
      <BreathingSpinner v-if="type=='breathing'" :src="src"/>
      <JumpingSpinner v-if="type=='jumping'" :src="src"/>
    </div>
    <div>
      <transition name="modal" v-if="showModal">
        <div class="modal-mask">
          <div class="modal-wrapper">
            <div class="modal-container">

              <div class="modal-header">
                <slot name="header">
                  {{modalTitle}}
                </slot>
              </div>
              <hr>
              <div class="modal-body">
                <slot name="body">
                  <div class="modal-content">
                  <pre>
&#60;template&#62;
  &#60;div&#62;
    &#60;{{spinner}} 
      :src="require(`@/assets/logo.png`)"
    &#62;
  &#60;/div&#62;
&#60;/template&#62;

import &#123; {{spinner}} &#125; from 'vue-image-spinner'
export default{
  components:&#123; {{spinner}} &#125;
}
                  </pre>
                  </div>
                </slot>
              </div>

              <div class="modal-footer">
                <slot name="footer">
                  <button class="modal-default-button" @click="showModal=false">
                    OK
                  </button>
                </slot>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import ScaleUpSpinner from '@/components/ScaleUpSpinner'
import RotatingSpinner from '@/components/RotatingSpinner'
import LinearRotatingSpinner from '@/components/LinearRotatingSpinner'
import BreathingSpinner from '@/components/BreathingSpinner'
import JumpingSpinner from '@/components/JumpingSpinner'

export default {
  name: 'app',
  components: {
    ScaleUpSpinner, RotatingSpinner, LinearRotatingSpinner, BreathingSpinner, JumpingSpinner
  },
  data(){
    return{
      type:'scaleup',
      src:require(`@/assets/logo.png`),
      showModal:false,
      modalTitle:'',
      modalBody:'',
      spinner:''
    }
  },
  /* eslint-disable */
  methods:{
    changeSpinner(v){
      this.type=v
    },
    getClass(v){
      if(v==this.type){
        return 'buttons active'
      }else{
        return 'buttons'
      }
    },
    showCode(v){
      switch(v){
        case 'scaleup':
          this.spinner='ScaleUpSpinner'
          this.modalTitle='ScaleUpSpinner'
          this.modalBody='codingan scaleup'
          break
        case 'rotating':
          this.spinner='RotatingSpinner'
          this.modalTitle='RotatingSpinner'
          this.modalBody='codingan rotating'
          break
        case 'linear':
          this.spinner='LinearRotatingSpinner'
          this.modalTitle='LinearRotatingSpinner'
          this.modalBody='codingan linear'
          break
        case 'breathing':
          this.spinner='BreathingSpinner'
          this.modalTitle='BreathingSpinner'
          this.modalBody='codingan breathing'
          break
        case 'jumping':
          this.spinner='JumpingSpinner'
          this.modalTitle='JumpingSpinner'
          this.modalBody='codingan jumping'
          break
      }
      this.showModal=true
    },
    spinnerContainer(){
        return{
            backgroundColor:'rgba(0,0,0, 0.5)'
        }
    },
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  }
  .spinner-container{
      position: fixed;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
  }
  .list-buttons{
    z-index:2
  }
  .buttons{
    margin-right:5px;
    margin-left:5px;
    margin-bottom:20px;
    height:25px;
    border-radius:5px;
  }

  .active{
    background-color:rgb(107, 107, 107);
    color:#fff
  }

  .modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 430px;
  margin: 0px auto;
  padding: 15px 20px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
  text-align: left;
}
.modal-content{
  overflow: 'scroll';
}

.modal-footer{
  padding-bottom: 20px
}

.modal-default-button {
  float: right;
  height:25px;
  width:70px;
  border-radius:5px;
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>
